<template>
    <div class="addAddress">
        <Header :isLeft="true" :title="title"></Header>
        <!-- 添加地址 -->
        <div class="viewbody">
            <div class="content">
                <FormBlock label="联系人" v-model="addressInfo.name" placeholder="姓名" :sexs="sexs" @checkSex="checkSex"
                    :sex="addressInfo.sex"></FormBlock>
                <FormBlock label="电话" v-model="addressInfo.phone" placeholder="手机号码"></FormBlock>
                <FormBlock label="地址" v-model="addressInfo.address" placeholder="小区/写字楼/学校楼" icon="angle-right"
                    @click="showSearch=true"></FormBlock>
                <FormBlock label="门牌号" v-model="addressInfo.bottom" placeholder="10号楼5单元404" icon="edit"
                    textarea="textarea"></FormBlock>
                <!-- 标签组件 -->
                <div class="formblock">
                    <div class="label-wrap">标签</div>
                    <TabTag :tags="tags" @checkTag="checkTag" :selectTag="addressInfo.tag"></TabTag>
                </div>

            </div>
            <!-- 确定按钮 -->
            <div class="form-button-wrap">
                <button @click="handleSave" class="form-button">确定</button>
            </div>
        </div>


        <!-- 点击地址跳转到搜索地址的组件 -->
        <AddressSearch :showSearch="showSearch" @close="showSearch = false" :addressInfo="addressInfo"></AddressSearch>

    </div>
</template>
<script>
    import Header from '../../components/Header'
    import FormBlock from '../../components/Orders/FormBlock'
    import TabTag from '../../components/Orders/TabTag'
    import AddressSearch from '../../components/Orders/AddressSearch'
    import { Toast } from 'mint-ui';


    export default {
        name: 'AddAddress',
        data() {
            return {
                title: '',
                tags: ["家", "学校", "公司"],
                sexs: ["先生", "女士"],

                // 选中的标签
                addressInfo: {},
                //搜索地址默认隐藏
                showSearch: false,
            }
        },
        beforeRouteEnter(to, from, next) {
            next(vm => {
                vm.title = to.params.title;
                vm.addressInfo = to.params.addressInfo
            }

            )

        },
        components: {
            Header,
            FormBlock,
            TabTag,
            AddressSearch
        },
        methods: {
            checkTag(item) {
                this.addressInfo.tag = item;
                // console.log(item);

            },
            checkSex(item) {
                this.addressInfo.sex = item;

            },
            // 点击确定按钮
            handleSave() {
                if (!this.addressInfo.name) {
                    this.showMsg("请输入联系人");
                    return;
                };
                if (!this.addressInfo.phone) {
                    this.showMsg("请输入手机号");
                    return;
                }
                if (!this.addressInfo.address) {
                    this.showMsg("请输入收货地址");
                    return;
                }

                if (this.title == '添加地址') {
                    this.addAddress();

                } else {
                    this.editAddress();
                }
            },
            showMsg(meg) {
                Toast({
                    message: meg,
                    position: 'bottom',
                    duration: 2000
                });
            },
            //添加地址
            addAddress() {
                this.$axios.post(
                    `/api/user/add_address/${localStorage.ele_token}`,
                    this.addressInfo
                )
                    .then(res => {
                        // 判断vuex中是否存有用户的地址，如果没有存储，先存储在跳转
                        if(!this.$store.getters.userInfo){
                            this.$stroe.dispatch('setUserInfo',this.addressInfo)
                        }
                        
                        this.$router.push("myAddress");
                    })
                    .catch(err => console.log(err));
            },
            // 编辑地址
            editAddress() {
                this.$axios.post(
                    `/api/user/edit_address/${localStorage.ele_token}/${
                    this.addressInfo._id
                    }`,
                    this.addressInfo
                )
                    .then(res => {
                        this.$router.push("/myAddress");
                    });
            }
        }

    }
</script>
<style scoped>
    .addAddress {
        width: 100%;
        height: 100%;
        box-sizing: border-box;
        padding-top: 45px;
    }

    .viewbody {
        width: 100%;
        height: 100%;
        overflow: auto;
        box-sizing: border-box;
        background-color: #f5f5f5;
    }

    .content {
        padding-left: 4vw;
        background: #fff;
        font-size: 0.94rem;
    }

    .formblock {
        background-color: #fff;
        border-bottom: 1px solid #eee;
        display: flex;
    }

    .formblock .label-wrap {
        flex-basis: 17.333333vw;
        padding: 3.733333vw 0;
        line-height: 4.8vw;
        color: #333;
        font-weight: 700;
    }

    /* 确定按钮 */
    .form-button-wrap {
        padding: 5.333333vw 4vw;
        display: flex;
    }

    .form-button-wrap .form-button {
        background: #00d762;
        text-align: center;
        border-radius: 0.533333vw;
        flex: 1;
        font-size: 1.1rem;
        line-height: 5.066667vw;
        color: #fff;
        padding: 3.333333vw 0;
        border: none;
        font-weight: 500;
    }
</style>