<template>
  <div class="login">
    <div class="logo">
      <img src="../assets/logo.jpg" alt="my logo">
    </div>
    <!-- 手机号 -->
    <InputGroup type="number" v-model="phone" placeholder="手机号" :btnTitle="btnTitle" :disabled="true"
      :error="errors.phone" @btnClick="getVerifyCode"></InputGroup>
    <!-- 验证码 -->
    <InputGroup type="number" v-model="verifyCode" placeholder="验证码" :error="errors.code"></InputGroup>
    <!-- 用户服务协议 -->
    <div class="login_des">
      <p>
        新用户登录即自动注册，表示已同意
        <span>《用户服务协议》</span>
      </p>
    </div>
    <!-- 登录按钮 -->
    <div class="login_btn">
      <button :disabled="isClick" @click="handleLogin">登录</button>
    </div>
  </div>
</template>
<script>

  import InputGroup from '../components/InputGroup.vue'
  export default {
    name: 'Login',
    components: {
      InputGroup: InputGroup
    },
    data() {
      return {
        phone: '17718372770',
        verifyCode: '002158',
        errors: {},
        btnTitle: '获取验证码',
        disabled: false,
      }
    },
    computed: {
      isClick() {
        if (!this.phone || !this.verifyCode) {
          return true;
        } else {
          return false;
        }

      }
    },
    methods: {
      // 点击登录按钮验证 手机号 和 密码是否正确
      handleLogin() {
        // 取消错误提醒
        this.errors = {};
        // 发送请求
        this.$axios.post("/api/posts/sms_back", {
                      phone: this.phone,
                      code: this.verifyCode
                    })
            .then((res)=>{
              // console.log(res.data);
              localStorage.setItem("ele_token", res.data.user._id);
              this.$router.push('/');
        })
        .catch((err)=>{
          // 返回错误信息
          // console.log(err);
          this.errors = {
            code: err.response.data.msg
          };
        })
      },
      //获取验证码
      getVerifyCode() {
        // 先判断手机号是否为空
        // 再判断手机号是否合法
        // 最后合法
        if (this.verifyCodePhone()) {
          this.verifyCodeTime();
          // 发送网络请求
          this.$axios
            .post("/api/posts/sms_send", {
              tpl_id: "195347",
              key: "050c230234c09f7495602fb37c37ed5f",
              phone: this.phone
            })
            .then(res => {
              console.log(res);
            });
        }

      },
      // 判断手机号是否合法
      verifyCodePhone() {
        if (!this.phone) {
          this.errors = {
            phone: '手机号不能为空'
          };
          return false
        } else if (!/^1[345678]\d{9}$/.test(this.phone)) {
          this.errors = {
            phone: '请填写合法的手机号'
          }
          return false
        } else {
          this.errors = {};
          return true
        }
      },
      // 验证码倒计时
      verifyCodeTime() {
        let time = 60;
        let timer = setInterval(() => {
          if (time == 0) {
            this.btnTitle = '获取验证码';
            clearInterval(timer);
            this.disabled = false;
          } else {
            this.btnTitle = time + '秒后重试';
            this.disabled = true;
            time--;
          }
        }, 1000)

      }
    }
  }
</script>

<style scoped>
  .login {
    width: 100%;
    height: 100%;
    padding: 30px;
    box-sizing: border-box;
    background: #fff;
  }

  .logo {
    text-align: center;
  }

  .logo img {
    width: 150px;
  }

  .text_group,
  .login_des,
  .login_btn {
    margin-top: 20px;
  }

  .login_des {
    color: #aaa;
    line-height: 22px;
  }

  .login_des span {
    color: #4d90fe;
  }

  .login_btn button {
    width: 100%;
    height: 40px;
    background-color: #48ca38;
    border-radius: 4px;
    color: white;
    font-size: 14px;
    border: none;
    outline: none;
  }

  .login_btn button[disabled] {
    background-color: #8bda81;
  }
</style>